
Template.__checkName("comp_auth");
Template["comp_auth"] = new Template("Template.comp_auth", (function() {
  var view = this;
  return HTML.DIV({
    "class": "auth-layout"
  }, "\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("authInProcess"));
  }, function() {
    return [ "\n			", HTML.P("loading ..."), "\n		" ];
  }, function() {
    return [ "\n			", Blaze.If(function() {
      return Spacebars.call(view.lookup("canShow"));
    }, function() {
      return [ "\n		  		", HTML.P("This is the area for adding a new post"), "\n		  		", HTML.DIV({
        "class": "js-logout"
      }, "\n					", HTML.I({
        "class": "material-icons"
      }, "exit_to_app"), "\n					Sign out\n				"), "\n			" ];
    }, function() {
      return [ "\n		  		", Spacebars.include(view.lookupTemplate("auth_signin")), "\n				", Spacebars.include(view.lookupTemplate("auth_signup")), "\n			" ];
    }), "\n		" ];
  }), "\n	");
}));

Template.__checkName("auth_signin");
Template["auth_signin"] = new Template("Template.auth_signin", (function() {
  var view = this;
  return [ HTML.Raw("<h1>Sign in</h1>\n	"), HTML.FORM({
    "class": "js-signin"
  }, "\n		", HTML.Raw('<input type="email" name="email" required="required" placeholder="Email Address">'), "\n		", HTML.Raw("<br>"), "\n		", HTML.Raw('<input type="password" name="password" required="required" placeholder="Password">'), "\n		", HTML.Raw("<br>"), "\n		", Blaze.View("lookup:message", function() {
    return Spacebars.mustache(view.lookup("message"));
  }), "\n		", HTML.Raw("<br>"), "\n		", HTML.Raw('<input type="submit" value="Sign In">'), "\n	") ];
}));

Template.__checkName("auth_signup");
Template["auth_signup"] = new Template("Template.auth_signup", (function() {
  var view = this;
  return HTML.FORM({
    "class": "js-signup"
  }, HTML.Raw('\n		<input type="text" name="name" required="required" placeholder="Your name">\n		<br>\n\n		<input type="email" name="email" class="js-email" required="required" placeholder="Email Address">\n		<br>\n		'), Blaze.View("lookup:checkMessage", function() {
    return Spacebars.mustache(view.lookup("checkMessage"));
  }), HTML.Raw('<br>\n		<input type="password" name="password" required="required" placeholder="Password">\n		<br>\n\n		<input type="password" name="confirm" required="required" placeholder="Re-type Password">\n		<br>\n		'), Blaze.View("lookup:message", function() {
    return Spacebars.mustache(view.lookup("message"));
  }), HTML.Raw('\n		<br>\n		<input type="submit" value="Sign Up">\n	'));
}));
