
Template.__checkName("App_main");
Template["App_main"] = new Template("Template.App_main", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "wrapper"
  }, "\n  ", HTML.HEADER({
    "class": "header"
  }, "\n  	", Spacebars.include(view.lookupTemplate("comp_header")), "\n  "), "\n  ", HTML.MAIN({
    "class": "main"
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      template: Spacebars.call(view.lookup("content"))
    };
  }, function() {
    return Spacebars.include(function() {
      return Spacebars.call(Template.__dynamic);
    });
  }), "\n  "), "\n  ", HTML.Raw('<footer class="footer">Meteor</footer>'), "\n"), "\n\n ", Spacebars.include(view.lookupTemplate("comp_auth")) ];
}));
